import { setPixel, saveImg, getMousePos } from '../canvas/canvas';

import { getColor } from '../colors/colors';

const canvas = document.getElementById('grid');

const mouse = {};

let x1;
let y1;

let draw = false;

function down(e) {
    const current = document.querySelector('#current');
    draw = true;
    mouse.x = getMousePos(e).x;
    mouse.y = getMousePos(e).y;
    x1 = mouse.x;
    y1 = mouse.y;

    setPixel(x1, y1, getColor(current));
}

function move(e) {
    if (draw === true) {
        const current = document.querySelector('#current');
        mouse.x = getMousePos(e).x;
        mouse.y = getMousePos(e).y;
        x1 = mouse.x;
        y1 = mouse.y;

        setPixel(x1, y1, getColor(current));
    }
}

function up() {
    draw = false;
    saveImg(canvas);
}

export { down, move, up };
