/* eslint-disable no-param-reassign */
const canvas = document.getElementById('grid');
const context = canvas.getContext('2d');

function swap(current, prev) {
    const currentColor = window.getComputedStyle(current).backgroundColor;
    const prevColor = window.getComputedStyle(prev).backgroundColor;
    const x = prevColor;
    prev.style.backgroundColor = currentColor;
    current.style.backgroundColor = x;
}

function colorSelect(e) {
    const prev = document.querySelector('#prev');
    const current = document.querySelector('#current');

    if (e.target.id === 'prev') {
        swap(prev, current);
    }

    if (e.target.id === 'current') {
        swap(current, prev);
    }
}

function getPixelColor(pixelPos) {
    const imgData = context.getImageData(0, 0, canvas.width, canvas.height);
    const { data } = imgData;
    return {
        r: +data[pixelPos],
        g: +data[pixelPos + 1],
        b: +data[pixelPos + 2],
        a: +data[pixelPos + 3],
    };
}
function getColor(colorPos) {
    const color = window.getComputedStyle(colorPos).backgroundColor;
    const start = 5;
    const end = -1;
    const a = 255;
    const colorRGBA = color.slice(start, end).split(',');

    return {
        r: +colorRGBA[0],
        g: +colorRGBA[1],
        b: +colorRGBA[2],
        a,
    };
}

function colorPixel(pixelPos) {
    const imgData = context.getImageData(0, 0, canvas.width, canvas.height);
    const current = document.querySelector('#current');
    const currentColor = getColor(current);
    imgData.data[pixelPos] = currentColor.r;
    imgData.data[pixelPos + 1] = currentColor.g;
    imgData.data[pixelPos + 2] = currentColor.b;
    imgData.data[pixelPos + 3] = currentColor.a;
    context.putImageData(imgData, 0, 0);
}

function getStartColor(x, y) {
    const imgData = context.getImageData(x, y, 1, 1);
    const { data } = imgData;
    return {
        r: +data[0],
        g: +data[1],
        b: +data[2],
        a: +data[3],
    };
}

export { colorSelect };
export { getPixelColor, colorPixel };
export { getStartColor, getColor };
