import { down, move, up } from '../settings/pen';
import { downRuler, moveRuler, upRuler } from '../settings/penRuler';
import { downEraser, moveEraser, upEraser } from '../settings/eraser';
import { downCircle, moveCircle, upCircle } from '../settings/circle';
import clickBucket from '../settings/bucket';
import clickFill from '../settings/fill';
import { downLighter, upLighter, moveLighter } from '../settings/lighter';
import { downDithering, upDithering, moveDithering } from '../settings/dithering';
import { downRectangle, upRectangle, moveRectangle } from '../settings/rectangle';
import { moveMouseCanvas } from './canvas';

const canvas = document.getElementById('grid');

function drawPen() {
    canvas.addEventListener('mousedown', down, false);
    canvas.addEventListener('mousemove', move, false);
    canvas.addEventListener('mouseup', up, false);
}

function removePen() {
    canvas.removeEventListener('mousedown', down, false);
    canvas.removeEventListener('mousemove', move, false);
    canvas.removeEventListener('mouseup', up, false);
}

function drawPenRuler() {
    canvas.addEventListener('mousedown', downRuler, false);
    canvas.addEventListener('mousemove', moveRuler, false);
    canvas.addEventListener('mouseup', upRuler, false);
}

function removePenRuler() {
    canvas.removeEventListener('mousedown', downRuler, false);
    canvas.removeEventListener('mousemove', moveRuler, false);
    canvas.removeEventListener('mouseup', upRuler, false);
}
function clearEraser() {
    canvas.addEventListener('mousedown', downEraser, false);
    canvas.addEventListener('mousemove', moveEraser, false);
    canvas.addEventListener('mouseup', upEraser, false);
}

function removeClearEraser() {
    canvas.removeEventListener('mousedown', downEraser, false);
    canvas.removeEventListener('mousemove', moveEraser, false);
    canvas.removeEventListener('mouseup', upEraser, false);
}

function setPaintBucket() {
    canvas.addEventListener('click', clickBucket, false);
}

function removePaintBucket() {
    canvas.removeEventListener('click', clickBucket, false);
}

function setFill() {
    canvas.addEventListener('click', clickFill, false);
}

function removeFill() {
    canvas.removeEventListener('click', clickFill, false);
}

function setLighter() {
    canvas.addEventListener('mousedown', downLighter, false);
    canvas.addEventListener('mousemove', moveLighter, false);
    canvas.addEventListener('mouseup', upLighter, false);
}

function removeLighter() {
    canvas.removeEventListener('mousedown', downLighter, false);
    canvas.removeEventListener('mousemove', moveLighter, false);
    canvas.removeEventListener('mouseup', upLighter, false);
}

function drawCircle() {
    canvas.addEventListener('mousedown', downCircle, false);
    canvas.addEventListener('mousemove', moveCircle, false);
    canvas.addEventListener('mouseup', upCircle, false);
}

function removeCircle() {
    canvas.removeEventListener('mousedown', downCircle, false);
    canvas.removeEventListener('mousemove', moveCircle, false);
    canvas.removeEventListener('mouseup', upCircle, false);
}

function setDithering() {
    canvas.addEventListener('mousedown', downDithering, false);
    canvas.addEventListener('mousemove', moveDithering, false);
    canvas.addEventListener('mouseup', upDithering, false);
}

function removeDithering() {
    canvas.removeEventListener('mousedown', downDithering, false);
    canvas.removeEventListener('mousemove', moveDithering, false);
    canvas.removeEventListener('mouseup', upDithering, false);
}
function setRectangle() {
    canvas.addEventListener('mousedown', downRectangle, false);
    canvas.addEventListener('mousemove', moveRectangle, false);
    canvas.addEventListener('mouseup', upRectangle, false);
}

function removeRectangle() {
    canvas.removeEventListener('mousedown', downRectangle, false);
    canvas.removeEventListener('mousemove', moveRectangle, false);
    canvas.removeEventListener('mouseup', upRectangle, false);
}

canvas.addEventListener('mousemove', moveMouseCanvas, false);

export { clearEraser, removeClearEraser };
export { drawPen, removePen };
export { setPaintBucket, removePaintBucket };
export { setFill, removeFill };
export { drawPenRuler, removePenRuler };
export { drawCircle, removeCircle };
export { setLighter, removeLighter };
export { setDithering, removeDithering };
export { setRectangle, removeRectangle };
