import { saveImg } from '../canvas/canvas';
import { showFrameCanvas } from '../frames/frames';

let elem = {};
const storage = {};
const canvas = document.getElementById('grid');

function rememberColor() {
    const current = document.querySelector('#current');
    const currentColor = window.getComputedStyle(current).backgroundColor;
    const prev = document.querySelector('#prev');
    const prevColor = window.getComputedStyle(prev).backgroundColor;

    const colors = {
        current: currentColor,
        prev: prevColor,
    };

    localStorage.setItem('colors', JSON.stringify(colors));
}
function rememberFrame() {
    const frames = document.getElementsByClassName('frame');

    for (let i = 0; i < frames.length; i += 1) {
        elem = {
            className: frames[i].className,
            html: frames[i].innerHTML,
        };
        storage[i] = elem;
        storage.length = i;
        elem = {};
    }

    localStorage.setItem('frames', JSON.stringify(storage));
}

function remember() {
    // localStorage.clear();
    rememberColor();
    rememberFrame();
}

function setFrames() {
    const frame = JSON.parse(localStorage.getItem('frames'));
    const frames = document.querySelector('.old-frame');
    console.log(frame);

    if (frame.length >= 0) frames.innerHTML = '';

    for (let i = 0; i <= frame.length; i += 1) {
        frames.innerHTML += `<div class="${frame[i].className}">${frame[i].html} </div>`;
    }

    const check = document.querySelector('.check');

    if (check) {
        showFrameCanvas(check.children[0]);
    }
}

function setColor() {
    const prev = document.getElementById('prev');
    const current = document.getElementById('current');
    const colors = JSON.parse(localStorage.getItem('colors'));
    prev.style.backgroundColor = colors.prev;
    current.style.backgroundColor = colors.current;
}

function getMemories() {
    setFrames();
    saveImg(canvas);
    setColor();
}
export { remember, getMemories };
