import './index.css';
import './settings/settings.css';
import './colors/colors.css';
import './frames/frames.css';
import './animation/animation.css';

import { setCursor, showKey } from './settings/settings';
import { remember, getMemories } from './storage/localStorage';
import setFrameSettings from './frames/settings';
import { animate } from './animation/animation';
import { colorSelect } from './colors/colors';
import { setSizeCanvas } from './canvas/canvas';

const palette = document.querySelector('.palette');
const speed = document.querySelector('.speed');
const colors = document.querySelector('.colors');
const frames = document.querySelector('.frames');
const sizeCanvas = document.querySelector('.size-canvas');

window.onload = function getStorage() {
    getMemories();

    colors.addEventListener('click', colorSelect, false);
    palette.addEventListener('click', setCursor, false);
    palette.addEventListener('mouseover', showKey, false);
    frames.addEventListener('click', setFrameSettings, false);
    speed.addEventListener('click', animate, false);
    sizeCanvas.addEventListener('click', setSizeCanvas, false);
};

window.onbeforeunload = function getStorage() {
    remember();
};
