/* eslint-disable object-curly-newline */
import { addFrame, removeFrame, copyFrame, showFrameCanvas } from './frames';

function setFrameSettings(e) {
    if (e.target.id === 'add' || e.target.id === 'add-child') addFrame();
    if (e.target.id === 'delete') removeFrame(e.target);
    if (e.target.id === 'copy') copyFrame(e.target);
    if (e.target.id === 'frame') showFrameCanvas(e.target.children[0]);
    if (e.target.id === 'img') showFrameCanvas(e.target);
}

export default setFrameSettings;
