import { saveImg, getMousePos } from '../canvas/canvas';

let x1;
let y1;

let draw = false;
const canvas = document.getElementById('grid');
const ctx = canvas.getContext('2d');

function downEraser(e) {
    draw = true;
    x1 = getMousePos(e).x;
    y1 = getMousePos(e).y;

    ctx.clearRect(x1, y1, 1, 1);
}

function moveEraser(e) {
    if (draw === true) {
        x1 = getMousePos(e).x;
        y1 = getMousePos(e).y;

        ctx.clearRect(x1, y1, 1, 1);
    }
}

function upEraser() {
    draw = false;
    saveImg(canvas);
}

export { downEraser, moveEraser, upEraser };
