import {
    drawPen,
    removePen,
    drawPenRuler,
    removePenRuler,
    clearEraser,
    removeClearEraser,
    setPaintBucket,
    removePaintBucket,
    setFill,
    removeFill,
    drawCircle,
    removeCircle,
    setLighter,
    removeLighter,
    setDithering,
    removeDithering,
    setRectangle,
    removeRectangle,
} from '../canvas/canvasListener';

import downloadGif from '../export/gif';
import { fullScreen } from '../animation/animation';

function setCursor(e) {
    if (e.target.id === 'pen' || e.target.id === 'pen-child') {
        document.body.style.cursor = "url('../src/images/pen.png') 0 100, auto";
        drawPen();
    } else if (e.target.id !== 'pen' || e.target.id !== 'pen-child') {
        removePen();
    }
    if (e.target.id === 'pen-ruler' || e.target.id === 'pen-ruler-child') {
        document.body.style.cursor = "url('../src/images/pen-ruler.png') 0 100, auto";
        drawPenRuler();
    } else if (e.target.id !== 'pen-ruler' || e.target.id !== 'pen-ruler-child') {
        removePenRuler();
    }
    if (e.target.id === 'circle' || e.target.id === 'circle-child') {
        document.body.style.cursor = "url('../src/images/circle.png') 50 50, auto";
        drawCircle();
    } else if (e.target.id !== 'circle' || e.target.id !== 'circle-child') {
        removeCircle();
    }
    if (e.target.id === 'eraser' || e.target.id === 'eraser-child') {
        document.body.style.cursor = "url('../src/images/eraser.png') 0 100, auto";
        clearEraser();
    } else if (e.target.id !== 'eraser' || e.target.id !== 'eraser-child') {
        removeClearEraser();
    }
    if (e.target.id === 'paint-bucket' || e.target.id === 'paint-bucket-child') {
        document.body.style.cursor = "url('../src/images/bucket.png') 100 100, auto";
        setPaintBucket();
    } else if (e.target.id !== 'paint-bucket' || e.target.id !== 'paint-bucket-child') {
        removePaintBucket();
    }
    if (e.target.id === 'fill' || e.target.id === 'fill-child') {
        document.body.style.cursor = "url('../src/images/fill.png') 0 0, auto";
        setFill();
    } else if (e.target.id !== 'fill' || e.target.id !== 'fill-child') {
        removeFill();
    }
    if (e.target.id === 'lighter' || e.target.id === 'lighter-child') {
        document.body.style.cursor = "url('../src/images/lighter.png') 0 50, auto";
        setLighter();
    } else if (e.target.id !== 'lighter' || e.target.id !== 'lighter-child') {
        removeLighter();
    }
    if (e.target.id === 'dithering' || e.target.id === 'dithering-child') {
        document.body.style.cursor = "url('../src/images/dithering.png') 0 50, auto";
        setDithering();
    } else if (e.target.id !== 'dithering' || e.target.id !== 'dithering-child') {
        removeDithering();
    }
    if (e.target.id === 'rectangle' || e.target.id === 'rectangle-child') {
        document.body.style.cursor = "url('../src/images/rectangle.png') 0 50, auto";
        setRectangle();
    } else if (e.target.id !== 'rectangle' || e.target.id !== 'rectangle-child') {
        removeRectangle();
    }
    if (e.target.id === 'download' || e.target.id === 'download-child') {
        downloadGif();
    }
}

function showKey(e) {
    const pen = document.querySelector('.pen');
    const penRuler = document.querySelector('.pen-ruler');
    const circle = document.querySelector('.circle');
    const bucket = document.querySelector('.bucket');
    const fill = document.querySelector('.fill');
    const eraser = document.querySelector('.eraser');
    const lighter = document.querySelector('.lighter');
    const dithering = document.querySelector('.dithering');
    const rectangle = document.querySelector('.rectangle');
    const download = document.querySelector('.download');

    if (e.target.id === 'pen' || e.target.id === 'pen-child') {
        pen.style.visibility = 'visible';
    } else pen.style.visibility = 'hidden';
    if (e.target.id === 'pen-ruler' || e.target.id === 'pen-ruler-child') {
        penRuler.style.visibility = 'visible';
    } else penRuler.style.visibility = 'hidden';
    if (e.target.id === 'circle' || e.target.id === 'circle-child') {
        circle.style.visibility = 'visible';
    } else circle.style.visibility = 'hidden';
    if (e.target.id === 'paint-bucket' || e.target.id === 'paint-bucket-child') {
        bucket.style.visibility = 'visible';
    } else bucket.style.visibility = 'hidden';
    if (e.target.id === 'fill' || e.target.id === 'fill-child') {
        fill.style.visibility = 'visible';
    } else fill.style.visibility = 'hidden';
    if (e.target.id === 'eraser' || e.target.id === 'eraser-child') {
        eraser.style.visibility = 'visible';
    } else eraser.style.visibility = 'hidden';
    if (e.target.id === 'lighter' || e.target.id === 'lighter-child') {
        lighter.style.visibility = 'visible';
    } else lighter.style.visibility = 'hidden';
    if (e.target.id === 'dithering' || e.target.id === 'dithering-child') {
        dithering.style.visibility = 'visible';
    } else dithering.style.visibility = 'hidden';
    if (e.target.id === 'rectangle' || e.target.id === 'rectangle-child') {
        rectangle.style.visibility = 'visible';
    } else rectangle.style.visibility = 'hidden';

    if (e.target.id === 'download' || e.target.id === 'download-child') {
        download.style.visibility = 'visible';
    } else download.style.visibility = 'hidden';
}

document.onkeyup = function getKey(e) {
    const penKey = 80;
    const strokeKey = 76;
    const circleKey = 67;
    const eraserKey = 69;
    const bucketKey = 66;
    const fillKey = 65;
    const lighterKey = 86;
    const ditheringKey = 84;
    const rectangleKey = 82;
    const downloadGifKey = 68;
    const fullScreenKey = 32;

    if (e.keyCode === penKey) {
        document.body.style.cursor = "url('../src/images/pen.png') 0 100, auto";
        drawPen();
    } else if (e.keyCode !== penKey) {
        removePen();
    }
    if (e.keyCode === strokeKey) {
        document.body.style.cursor = "url('../src/images/pen-ruler.png') 0 100, auto";
        drawPenRuler();
    } else if (e.keyCode !== strokeKey) {
        removePenRuler();
    }
    if (e.keyCode === circleKey) {
        document.body.style.cursor = "url('../src/images/circle.png') 50 50, auto";
        drawCircle();
    } else if (e.keyCode !== circleKey) {
        removeCircle();
    }
    if (e.keyCode === eraserKey) {
        document.body.style.cursor = "url('../src/images/eraser.png') 0 100, auto";
        clearEraser();
    } else if (e.keyCode !== eraserKey) {
        removeClearEraser();
    }
    if (e.keyCode === bucketKey) {
        document.body.style.cursor = "url('../src/images/bucket.png') 100 100, auto";
        setPaintBucket();
    } else if (e.keyCode !== bucketKey) {
        removePaintBucket();
    }
    if (e.keyCode === fillKey) {
        document.body.style.cursor = "url('../src/images/fill.png') 0 0, auto";
        setFill();
    } else if (e.keyCode !== fillKey) {
        removeFill();
    }
    if (e.keyCode === lighterKey) {
        document.body.style.cursor = "url('../src/images/lighter.png') 0 50, auto";
        setLighter();
    } else if (e.keyCode !== lighterKey) {
        removeLighter();
    }
    if (e.keyCode === ditheringKey) {
        document.body.style.cursor = "url('../src/images/dithering.png') 0 50, auto";
        setDithering();
    } else if (e.keyCode !== ditheringKey) {
        removeDithering();
    }
    if (e.keyCode === rectangleKey) {
        document.body.style.cursor = "url('../src/images/rectangle.png') 0 50, auto";
        setRectangle();
    } else if (e.keyCode !== rectangleKey) {
        removeRectangle();
    }
    if (e.keyCode === downloadGifKey) {
        //  document.body.style.cursor = "url('../src/images/move.png') 50 50, auto";
        downloadGif();
    }
    if (e.keyCode === fullScreenKey) {
        fullScreen();
    }
};
export { setCursor, showKey };
