import { drawRectangle, saveImg, getMousePos } from '../canvas/canvas';

import { getColor } from '../colors/colors';

const canvas = document.getElementById('grid');
const context = canvas.getContext('2d');

let x1;
let y1;
let x2;
let y2;
let draw = false;
let save;

function downRectangle(e) {
    draw = true;
    x1 = getMousePos(e).x;
    y1 = getMousePos(e).y;
    save = context.getImageData(0, 0, canvas.clientWidth, canvas.height);
}

function moveRectangle(e) {
    if (draw === true) {
        const current = document.querySelector('#current');
        x2 = getMousePos(e).x;
        y2 = getMousePos(e).y;

        context.putImageData(save, 0, 0);
        drawRectangle(x1, y1, x2, y2, getColor(current));
    }
}

function upRectangle() {
    const current = document.querySelector('#current');
    draw = false;

    context.putImageData(save, 0, 0);
    drawRectangle(x1, y1, x2, y2, getColor(current));
    saveImg(canvas);
}
export { downRectangle, upRectangle, moveRectangle };
