import { saveImg, getMousePos } from '../canvas/canvas';

const canvas = document.getElementById('grid');
const context = canvas.getContext('2d');
const mouse = {};

let light = false;

function lighterColor(pos) {
    const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
    const pixels = imageData.data;
    const lighter = 50;
    pixels[pos + 3] = imageData.data[pos + 3] - lighter; // Green
    context.putImageData(imageData, 0, 0);
}

function downLighter(e) {
    light = true;
    mouse.x = getMousePos(e).x;
    mouse.y = getMousePos(e).y;
    const pos = (mouse.y * canvas.width + mouse.x) * 4;
    lighterColor(pos);
    saveImg(canvas);
}

function moveLighter(e) {
    if (light === true) {
        mouse.x = getMousePos(e).x;
        mouse.y = getMousePos(e).y;

        const pos = (mouse.y * canvas.width + mouse.x) * 4;
        lighterColor(pos);
    }
}

function upLighter() {
    light = false;
    saveImg(canvas);
}
export { downLighter, upLighter, moveLighter };
