import { drawCircle, saveImg, getMousePos } from '../canvas/canvas';

import { getColor } from '../colors/colors';

let x1;
let y1;
let x2;
let y2;
let save;
let draw = false;

const canvas = document.getElementById('grid');
const context = canvas.getContext('2d');
const current = document.getElementById('current');

function downCircle(e) {
    draw = true;
    x1 = getMousePos(e).x;
    y1 = getMousePos(e).y;
    save = context.getImageData(0, 0, canvas.clientWidth, canvas.height);
}

function moveCircle(e) {
    if (draw === true) {
        x2 = getMousePos(e).x;
        y2 = getMousePos(e).y;

        context.putImageData(save, 0, 0);
        drawCircle(x1, y1, x2, y2, getColor(current));
    }
}

function upCircle() {
    draw = false;
    context.putImageData(save, 0, 0);
    drawCircle(x1, y1, x2, y2, getColor(current));
    saveImg(canvas);
}

export { downCircle, moveCircle, upCircle };
