import { save, saveImg } from '../canvas/canvas';

const canvas = document.getElementById('grid');
const context = canvas.getContext('2d');
const frames = document.querySelector('.old-frame');
const frame = document.getElementsByClassName('frame');

const { width, height } = canvas;

function drawFrameCanvas(elem) {
    const img = new Image();
    img.src = elem.src;
    context.clearRect(0, 0, canvas.width, canvas.height);
    context.drawImage(img, 0, 0);
}

function checkFrame(elem) {
    const { length } = frames.children;
    for (let i = 0; i < length; i += 1) {
        frames.children[i].classList.remove('check');
    }
    elem.classList.add('check');
}

function createDelElem() {
    const del = document.createElement('button');
    del.classList = 'fas fa-trash-alt';
    del.id = 'delete';
    return del;
}

function addFrame() {
    let del = createDelElem();
    const move = document.createElement('button');
    move.classList = 'fas fa-arrows-alt';
    move.id = 'move';
    const length = frame.length - 1;

    if (length === 0 && frame[0].lastChild !== del) {
        frame[0].appendChild(del);
        frame[0].appendChild(move);
    }

    frames.innerHTML += `<div class="frame shadow"id="frame">
                         <button class="far fa-copy" id="copy"></button>
                         <button class="fas fa-arrows-alt" id="move"></button>
                         <button class="fas fa-trash-alt" id='delete'></button>
                         </div>`;

    const newFrame = frames.children[length + 1];
    checkFrame(newFrame);

    del = frame[0].lastChild;
    context.clearRect(0, 0, canvas.clientWidth, canvas.height);
    save();
    saveImg(canvas);
}

function removeFrame(elem) {
    const delFrame = document.querySelector('#delete');
    const moveFrame = document.querySelector('#move');

    const length = frame.length - 1;

    if (elem.parentNode === frame[0]) {
        frames.removeChild(frame[0]);
        if (length === 1) {
            const delFrame2 = document.querySelector('#delete');
            const moveFrame2 = document.querySelector('#move');
            frame[0].removeChild(moveFrame2);
            frame[0].removeChild(delFrame2);
        }
        return;
    }

    if (length === 1) {
        frame[0].removeChild(moveFrame);
        frame[0].removeChild(delFrame);
    }
    const prevFrame = frames.children[length - 1];
    const prevFrameCanvas = prevFrame.children[0];

    frames.removeChild(elem.parentNode);
    checkFrame(prevFrame);
    drawFrameCanvas(prevFrameCanvas);
    saveImg(canvas);
}

function copyFrame(elem) {
    let del = createDelElem();
    const length = frame.length - 1;
    const img = elem.parentNode.firstChild;

    if (length === 0 && frame[0].lastChild !== del) frame[0].appendChild(del);

    const copy = document.createElement('div');
    copy.classList = 'frame shadow';

    if (img.width) {
        canvas.width = img.width;
        canvas.height = img.height;

        context.drawImage(img, 0, 0);

        copy.innerHTML = `<img src='${img.src}' id="img">
                          <button class="far fa-copy" id="copy"></button>
                          <button class="fas fa-arrows-alt" id="move"></button>
                          <button class="fas fa-trash-alt" id='delete'></button>`;
    } else {
        canvas.width = width;
        canvas.height = height;

        copy.innerHTML = `</button><button class="far fa-copy" id="copy"></button>
                          <button class="fas fa-arrows-alt" id="move"></button>
                          <button class="fas fa-trash-alt" id='delete'>`;
    }

    const nextFrame = elem.parentNode.nextSibling;
    const prevFrame = elem.parentNode;

    frames.insertBefore(copy, nextFrame);
    checkFrame(nextFrame);
    save(prevFrame, true);
    del = frame[0].lastChild;
    return canvas;
}

function showFrameCanvas(img) {
    const currentFrame = img.parentNode;
    checkFrame(currentFrame);
    drawFrameCanvas(img);
    save(currentFrame);
    saveImg(canvas);
}

export { addFrame, removeFrame };
export { copyFrame, showFrameCanvas, drawFrameCanvas };
