/* eslint-disable comma-dangle */
/* eslint-disable arrow-parens */
import { size } from '../canvas/canvas';

const preview = document.querySelector('.view');
const speed = [];
let timerSimple = 0;
let timerTwo = 0;
let timerFour = 0;
let timerSix = 0;
let i = 0;

function removePrevTimer() {
    if (timerSimple) {
        clearInterval(timerSimple);
        timerSimple = 0;
    }

    if (timerTwo) {
        clearInterval(timerTwo);
        timerTwo = 0;
    }

    if (timerFour) {
        clearInterval(timerFour);
        timerFour = 0;
    }

    if (timerSix) {
        clearInterval(timerSix);
        timerSix = 0;
    }
}

function running() {
    const imgs = Object.values(document.querySelectorAll('.frame img'));
    const url = imgs.map((value) => value.src);
    const l = url.length;

    if (l === 1) {
        removePrevTimer();
    }

    if (url[i]) {
        preview.innerHTML = `<img src="${url[i]}" class="${size[0]}">`;
    }
    i += 1;
    if (i >= l) i = 0;
}

function setNewTimer(elem) {
    if (elem.id === 'run') {
        timerSimple = setInterval(running, 0);
        speed[0] = 'timerSimple';
        return timerSimple;
    }

    if (elem.id === 'two') {
        timerTwo = setInterval(running, 1000);
        speed[0] = 'timerTwo';
        return timerTwo;
    }

    if (elem.id === 'four') {
        timerFour = setInterval(running, 500);
        speed[0] = 'timerFour';
        return timerFour;
    }

    if (elem.id === 'six') {
        timerSix = setInterval(running, 100);
        speed[0] = 'timerSix';
        return timerSix;
    }
    return true;
}

function animate(e) {
    removePrevTimer();
    setNewTimer(e.target);
}

function fullScreen() {
    if (!document.fullscreenElement) {
        preview.requestFullscreen();
    } else if (document.exitFullscreen) {
        document.exitFullscreen();
    }
}
function sum(a, b) {
    return a + b;
}
export { sum };
export { animate };
export { speed };
export { fullScreen };
