import { saveImg, getMousePos } from '../canvas/canvas';
import { getColor } from '../colors/colors';

const canvas = document.getElementById('grid');
const context = canvas.getContext('2d');
const canvas2 = document.getElementById('grid2');
const context2 = canvas2.getContext('2d');
const mouse = {};

let draw = false;

function startColor() {
    const current = document.getElementById('current');
    const prev = document.getElementById('prev');
    const imageData = context2.getImageData(0, 0, canvas2.width, canvas2.height);
    const pixels = imageData.data;
    const pixelLength = canvas.width * canvas.height;
    let j = 1;

    function cell(i, color) {
        pixels[i * 4] = color.r;
        pixels[i * 4 + 1] = color.g;
        pixels[i * 4 + 2] = color.b;
        pixels[i * 4 + 3] = color.a;
    }

    for (let i = 0; i < pixelLength; i += 2) {
        cell(i, getColor(current));

        if (canvas.width * j === i + 1) {
            i -= 1;
            j += 1;
        }
        if (canvas.width * j === i + 2) {
            i += 1;
            j += 1;
        }
    }

    j = 1;
    for (let i = 1; i < pixelLength; i += 2) {
        cell(i, getColor(prev));

        if (canvas.width * j === i + 1) {
            i -= 1;
            j += 1;
        }
        if (canvas.width * j === i + 2) {
            i += 1;
            j += 1;
        }
    }

    context2.putImageData(imageData, 0, 0);
}

function colorPixel(pixelPos) {
    const imgData1 = context.getImageData(0, 0, canvas.width, canvas.height);
    const imgData2 = context2.getImageData(0, 0, canvas2.width, canvas2.height);

    imgData1.data[pixelPos] = imgData2.data[pixelPos];
    imgData1.data[pixelPos + 1] = imgData2.data[pixelPos + 1];
    imgData1.data[pixelPos + 2] = imgData2.data[pixelPos + 2];
    imgData1.data[pixelPos + 3] = imgData2.data[pixelPos + 3];
    context.putImageData(imgData1, 0, 0);
}

function drawCells(e) {
    mouse.x = getMousePos(e).x;
    mouse.y = getMousePos(e).y;
    const pos = (mouse.y * canvas.width + mouse.x) * 4;
    colorPixel(pos);
}

function downDithering(e) {
    startColor();
    draw = true;
    drawCells(e);
}

function moveDithering(e) {
    if (draw === true) {
        drawCells(e);
    }
}

function upDithering() {
    draw = false;
    saveImg(canvas);
}

export { downDithering, upDithering, moveDithering };
