/* eslint-disable comma-dangle */
/* eslint-disable arrow-parens */
import { speed } from '../animation/animation';

const gifshot = require('gifshot');
const download = require('downloadjs');

function intervalAnimation() {
    const speedGif = speed[0];
    let animateSpeed;
    if (speedGif === 'timerTwo') animateSpeed = 1;
    else if (speedGif === 'timerFour') animateSpeed = 0.5;
    else if (speedGif === 'timerSix') animateSpeed = 0.1;
    else animateSpeed = 0;
    return animateSpeed;
}

function getFrameImg() {
    const imgs = Object.values(document.querySelectorAll('.frame img'));
    const url = imgs.map((value) => value.src);
    return url;
}

function downloadGif() {
    const images = getFrameImg();
    const interval = intervalAnimation();

    gifshot.createGIF(
        {
            images,
            interval,
            gifWidth: 32,
            gifHeight: 32,
            sampleInterval: 1,
        },
        (obj) => {
            if (!obj.error) {
                const { image } = obj;
                download(image, 'image.gif', 'image/gif');
            }
        }
    );
}

export default downloadGif;
