/* eslint-disable no-continue */
import './canvas.css';

const size = [];

function setPixel(x, y, color) {
    const canvas = document.getElementById('grid');
    const context = canvas.getContext('2d');
    const pixcel = context.createImageData(1, 1);

    pixcel.data[0] = color.r;
    pixcel.data[1] = color.g;
    pixcel.data[2] = color.b;
    pixcel.data[3] = color.a;

    context.putImageData(pixcel, x, y);
}

let savePosition;

function save(position, bool) {
    if (!position) savePosition = false;
    else if (bool) savePosition = position.nextSibling;
    else savePosition = position;
}

function saveImg(saveCanvas) {
    const preview = document.querySelector('.view');
    const dataURL = saveCanvas.toDataURL();
    const frame = document.getElementsByClassName('frame');
    const length = frame.length - 1;
    const tagsPosZero = `<img src='${dataURL}' id="img" class='${size[0]}'> 
                         <button class="far fa-copy" id="copy"></button>`;
    const tagsPosMoreThanZero = `<img src='${dataURL}' id="img" class='${size[0]}'> 
                                <button class="far fa-copy" id="copy"></button>
                                <button class="fas fa-arrows-alt" id="move"></button>
                                <button class="fas fa-trash-alt" id="delete" />`;

    if (savePosition) {
        if (!length) {
            savePosition.innerHTML = tagsPosZero;
        } else {
            savePosition.innerHTML = tagsPosMoreThanZero;
        }
    }

    if (!savePosition) {
        if (!length) {
            frame[length].innerHTML = tagsPosZero;
        } else {
            frame[length].innerHTML = tagsPosMoreThanZero;
        }
    }

    preview.innerHTML = `<img src='${dataURL}' class='${size[0]}'>`;
}

function drawImage(elem) {
    const canvas = document.getElementById('grid');
    const context = canvas.getContext('2d');
    const img = new Image();
    img.src = elem.src;
    context.clearRect(0, 0, canvas.width, canvas.height);
    context.drawImage(img, 0, 0);
}

function drawImageCanvaSize() {
    const canvas = document.getElementById('grid');
    const frames = document.getElementsByClassName('frame');
    for (let i = 0; i < frames.length; i += 1) {
        const elem = frames[i].children[0];
        drawImage(elem);
        save(frames[i]);
        saveImg(canvas);
    }
}

function setSize(canvas, square, transform, top, left) {
    canvas.width = square;
    canvas.height = square;
    canvas.style.transform = `scale(${transform}, ${transform})`;
    canvas.style.top = `${top}em`;
    canvas.style.left = `${left}em`;
}

function setSizeCanvas(e) {
    const canvas = document.getElementById('grid');
    const canvas2 = document.getElementById('grid2');
    if (e.target.id === 'first-size') {
        const square = 32;
        const transform = 18;
        const top = 21.3;
        const left = 23.25;
        setSize(canvas, square, transform, top, left);
        setSize(canvas2, square, transform, top, left);
        size[0] = 'first';
        drawImageCanvaSize();
    } else if (e.target.id === 'second-size') {
        const square = 64;
        const transform = 10;
        const top = 19.3;
        const left = 22.25;
        setSize(canvas, square, transform, top, left);
        setSize(canvas2, square, transform, top, left);
        size[0] = 'second';
        drawImageCanvaSize();
    } else if (e.target.id === 'third-size') {
        const square = 128;
        const transform = 5;
        const top = 17.3;
        const left = 20.25;
        setSize(canvas, square, transform, top, left);
        setSize(canvas2, square, transform, top, left);
        size[0] = 'third';
        drawImageCanvaSize();
    }
}

function drawRuler(x1, y1, x2, y2, color) {
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    const signX = x1 < x2 ? 1 : -1;
    const signY = y1 < y2 ? 1 : -1;
    let error = deltaX - deltaY;
    let x = x1;
    let y = y1;
    setPixel(x2, y2, color);

    while (x !== x2 || y !== y2) {
        setPixel(x, y, color);

        const error2 = error * 2;

        if (error2 > -deltaY) {
            error -= deltaY;
            x += signX;
        }
        if (error2 < deltaX) {
            error += deltaX;
            y += signY;
        }
    }
}

function drawCircle(x1, y1, x2, y2, color) {
    const deltaX = Math.abs(x2 - x1);
    const deltaY = Math.abs(y2 - y1);
    let r = deltaX + deltaY;
    let x = 0;
    let gap = 0;
    let delta = 2 - 2 * r;

    while (r >= 0) {
        setPixel(x1 + x, y1 - r, color);
        setPixel(x1 - x, y1 - r, color);
        setPixel(x1 - x, y1 + r, color);
        setPixel(x1 + x, y1 + r, color);

        gap = 2 * (delta + r) - 1;

        if (delta < 0 && gap <= 0) {
            delta += 2 * x + 1;
            continue;
        }
        if (delta > 0 && gap > 0) {
            r -= 1;
            delta -= 2 * r + 1;
            continue;
        }

        x += 1;
        delta += 2 * (x - r);
        r -= 1;
    }
}

function drawRectangle(x1, y1, x2, y2, color) {
    drawRuler(x1, y1, x2, y1, color);
    drawRuler(x2, y1, x2, y2, color);
    drawRuler(x2, y2, x1, y2, color);
    drawRuler(x1, y2, x1, y1, color);
}

function getMousePos(e) {
    const canvas = document.getElementById('grid');
    const rect = canvas.getBoundingClientRect();
    const scaleX = canvas.width / rect.width;
    const scaleY = canvas.height / rect.height;

    return {
        x: Math.floor((e.clientX - rect.left) * scaleX),
        y: Math.floor((e.clientY - rect.top) * scaleY),
    };
}

function showSizeCanvas() {
    const canvas = document.getElementById('grid');
    const sizeCanva = document.querySelector('.size');
    sizeCanva.innerHTML = `${canvas.width}x${canvas.height}`;
}

function moveMouseCanvas(e) {
    const canvas = document.getElementById('grid');

    const coordinates = document.querySelector('.coordinates');
    const x1 = getMousePos(e).x;
    const y1 = getMousePos(e).y;

    if (x1 >= canvas.width || y1 >= canvas.height) {
        coordinates.style.visibility = 'hidden';
    } else {
        coordinates.style.visibility = 'visible';
        coordinates.innerHTML = `${x1}:${y1}`;
    }
    showSizeCanvas();
}

export { setPixel };
export { drawRuler };
export { saveImg };
export { drawCircle };
export { moveMouseCanvas };
export { getMousePos };
export { save };
export { drawRectangle };
export { setSizeCanvas, size };
