/* eslint-disable object-curly-newline */
/* eslint-disable no-plusplus */
import { getPixelColor, colorPixel, getStartColor } from '../colors/colors';

import { saveImg, getMousePos } from '../canvas/canvas';

const canvas = document.getElementById('grid');

const stack = [];

let startColor;

function matchStartColor(pixelPos) {
    const pixelColor = getPixelColor(pixelPos);
    const { r, g, b, a } = pixelColor;
    return r === startColor.r && g === startColor.g && b === startColor.b && a === startColor.a;
}

function fillCanvas() {
    while (stack.length) {
        const newPos = stack.pop();
        const x = newPos[0];
        let y = newPos[1];
        let pixelPos = (y * canvas.width + x) * 4;
        while (--y >= -1 && matchStartColor(pixelPos)) {
            pixelPos -= canvas.width * 4;
        }
        pixelPos += canvas.width * 4;
        ++y;
        let left = false;
        let right = false;
        while (y++ <= canvas.height && matchStartColor(pixelPos)) {
            colorPixel(pixelPos);

            if (x > 0) {
                if (matchStartColor(pixelPos - 4)) {
                    if (!left) {
                        stack.push([x - 1, y]);
                        left = true;
                    } else if (left) left = false;
                }
            }
            if (x <= canvas.width) {
                if (matchStartColor(pixelPos + 4)) {
                    if (!right) {
                        stack.push([x + 1, y]);
                        right = true;
                    } else if (right) right = false;
                }
            }
            pixelPos += canvas.width * 4;
        }
    }
    saveImg(canvas);
}

function clickBucket(e) {
    const { x, y } = getMousePos(e);
    startColor = getStartColor(x, y);
    stack.push([x, y]);
    fillCanvas();
}
export default clickBucket;
